firebase.initializeApp (firebaseConfig);
var db = firebase.database();


function getUser() {
	if (firebase.auth().currentUser != null) return firebase.auth().currentUser;
	return null;
}

function getAdmin() {
	if (firebase.auth().currentUser != null && firebase.auth().currentUser.email === "dhf360@gmail.com") return firebase.auth().currentUser;
	return null;
}

class Section extends React.Component {
	constructor(props){
		super(props);
		this.focus = this.focus.bind(this);
		this.refSpan= React.createRef();
	}

	focus() {
		this.refSpan.current.focus();
	}
	
	render() {
		return (<div tabIndex="-1" data-focusid={this.props['data-focusid']} ref={this.refSpan}>{this.props.children}</div>);
	}
}

class Board extends React.Component {
	static defaultProps = {autoFocus:true}
	constructor(props) {
		super(props);
		this.childrenRefs = {};
		this.refHead= React.createRef();
	}

	createContent() {
		const items = this.props.content;
		if (!items) return undefined;
		var refs = {};
		function walk (items, lastSection) {
			let children = []
			for (let k in items) {
				k = items[k];
				if (typeof k === "string") {
					children.push(k)
					continue;
				}
				if (!k.t) continue;
				let ref = undefined;
				let focusId = lastSection;
				let e = k.t;
				let c = k.c;
				if (e === "table") c = [new element("tbody", c)];
				else if (e === 'Sec'){
					ref = React.createRef();
					refs[k.id] = ref;
					e = Section;
					focusId = k.id;
				}
				children.push(React.createElement(e, {key: k.id, ref:ref, 'data-focusid':focusId, children: walk(c, focusId)}));
			}
			return children;
		}
		var c = walk(items);
		this.childrenRefs = refs;
		return c;
	}

	setRefFocus(key) {
		this.childrenRefs[key] && this.childrenRefs[key].current.focus();
		envStorage.boards[envStorage.currentBoard].lastFocusId = key;
	}
	
	setLastFocus() {	
		if (this.props.boardData && this.props.boardData.lastFocusId) this.setRefFocus(this.props.boardData.lastFocusId);
		else this.refHead.current.focus();
	}

	componentDidUpdate() {
		if (this.props.autoFocus) this.setLastFocus();
	}

	componentDidMount() {
		if (this.props.autoFocus) this.setLastFocus();
	}

	render() {
		return (<div onClick={this.props.onClick}>
		<h1 tabIndex="-1" ref={this.refHead}>{this.props.title}</h1>
		{this.createContent()}
		</div>);
	}
}

class EditBoard extends React.Component {
	constructor(props){
		super(props);
		this.handleContentChange = this.handleContentChange.bind(this);
		this.handleTitleChange = this.handleTitleChange.bind(this);
		this.state = {title: '', content: ''};
	}

	handleTitleChange(e) {
		this.setState({title: e.target.value});
	}

	handleContentChange(e) {
		this.setState({content: e.target.value});
	}

	render() {
		return (<div>
			<h1>preview</h1>
			<Board title={this.state.title} content={textToBoardData(this.state.content)} autoFocus={false}/>
			<h1>Input</h1>
			<label htmlFor="input-title">
				Enter title
			</label>
			<input autoFocus type="text" id="input-title" onChange={this.handleTitleChange} defaultValue={this.state.title}/>
			<label htmlFor="input-content">
				Enter some content
			</label>
			<textarea id="input-content" onChange={this.handleContentChange} defaultValue={this.state.content}/>
			<button onClick={()=> this.props.onDone({title: this.state.title, content:this.state.content})}>Done!</button>
			<button onClick={this.props.onCancel}>Cancel</button>
		</div>);
	}
}

class ComposeMsg extends React.Component {
	constructor(props){
		super(props);
		this.state = {msg: ''};
		this.handleMsgChange = this.handleMsgChange.bind(this);
	}

	handleMsgChange(e) {
		this.setState({msg: e.target.value});
	}

	render() {
		return (<div>
			<h1>write a message for the speaker</h1>
			<label htmlFor="input-message">
				Enter message
			</label>
			<textarea autoFocus id="input-msg" onChange={this.handleMsgChange} defaultValue={this.state.msg}/>
			<button onClick={()=> this.props.onDone(this.state.msg)}>Done!</button>
			<button onClick={this.props.onCancel}>Cancel</button>
		</div>);
	}
}

class MainBoard extends React.Component {
	constructor(props){
		super(props);
		this.handleClickPrior = this.handleClickPrior.bind(this);
		this.handleClickNext = this.handleClickNext.bind(this);
		this.handleClickBoard = this.handleClickBoard.bind(this);
		this.onDoneBoard = this.onDoneBoard.bind(this);
		this.onDoneMsg = this.onDoneMsg.bind(this);
		this.refBoard= React.createRef();
		this.state = {currentBoard: 0, editing:false, editCurrent:false, writingMsg:false};
		this.firstTime=false;
	}

	componentDidMount () {
		this.listenEvents();
	}

	listenEvents() {
		this.firstTime=true;

		db.ref('boards').once('value').then(e => {
			let val = e.val();
			if (val) Object.values(val).forEach(v => {
				envStorage.boards.push(new BoardStorage(v.title, v.content));
			});
		});
		
		db.ref('boards').endAt().limitToLast(1).on('child_added', e => {
			if (this.firstTime) {
				this.firstTime = false;
				return;
			}
			let val = e.val();
			console.log("ultimo boardData", val);
			if (val){
				envStorage.boards.push(new BoardStorage(val.title, val.content));
			}
		});
		
		db.ref('lastFocusId').on('value', e => {
			let val = e.val();
			console.log("ultimo focus", val);
			if (val){
				this.refBoard.current.setRefFocus(val);
				envStorage.lastFocusId = val;
			}
		});

		db.ref('lastBoardId').on('value', e => {
			let val = e.val();
			console.log("ultimo lastBoardId", val, "tipo de datos", typeof val);
			if (typeof val == "number"){
				this.changeBoard(val, true);
				envStorage.lastBoardId = val;
			}
		});
	}

	handleClickBoard(e) {
		var id = e.target.dataset.focusid;
		if (!id) return;
		envStorage.boards[envStorage.currentBoard].lastFocusId = id;
		if (getAdmin()) db.ref("lastFocusId").set(id);
	}

	changeBoard(newCur, external) {
		if (newCur >= 0 && newCur < envStorage.boards.length) {
			envStorage.currentBoard = newCur;
			this.setState({currentBoard: newCur});
			if (!external && getAdmin()) db.ref("lastBoardId").set(newCur);
		}
	}

	handleClickPrior() {
		var cur = this.state.currentBoard;
		--cur;
		this.changeBoard(cur);
	}

	handleClickNext() {
		var cur = this.state.currentBoard;
		++cur;
		this.changeBoard(cur);
	}

	onDoneBoard(e) {
		// if (this.state.editCurrent) this.updateBoard(e);
		var l = envStorage.boards.length;
		this.setState({editing:false});
		if (getAdmin()) {
			db.ref("boards").push(e);
			setTimeout(() => db.ref("lastBoardId").set(l), 1000);
		}
	}

	onDoneMsg(e) {
		this.setState({writingMsg:false});
		var user = getUser();
		if (user) db.ref("msgs").push({name:user.displayName, msg:e});
	}



	render() {
		if (this.state.editing) return <EditBoard onDone={this.onDoneBoard}
		onCancel={()=> this.setState({editing:false, editCurrent:false})}/>;
		if (this.state.writingMsg) return <ComposeMsg onDone={this.onDoneMsg}
		onCancel={()=> this.setState({writingMsg:false})}/>;
		return (<div aria-live="assertive">
			<Board
			title={envStorage.boards[envStorage.currentBoard].title}
			content={envStorage.boards[envStorage.currentBoard].parsedContent}
			onClick={this.handleClickBoard}
			boardData={envStorage.boards[envStorage.currentBoard]}
			ref={this.refBoard}/>
			<section aria-label="board controls">
				<button onClick={this.handleClickPrior}>previous board</button>
				<button onClick={this.handleClickNext}>Next board</button>
				{getAdmin() && <button onClick={e=> this.setState({editing:true})}>New board</button>}
				{getUser() && <button onClick={e=> this.setState({writingMsg:true})}>ask  the speaker</button>}
			</section>
		</div>);
	}
}

class AttendeesMsg extends React.Component {
	constructor(props){
		super(props);
		this.state = {msgs: {}};
		this.cleanBoards = this.cleanBoards.bind(this);
		this.cleanMsgs= this.cleanMsgs.bind(this);
	}

	componentDidMount () {
		this.listenEvents();
	}

	listenEvents() {
		this.firstTime=true;

		db.ref('msgs').once('value').then(e => {
			let val = e.val();
			if (val) this.setState({msgs: val});
		});
		
		db.ref('msgs').endAt().limitToLast(1).on('child_added', e => {
			if (this.firstTime) {
				this.firstTime = false;
				return;
			}
			let val = e.val();
			if (val){
				this.setState({msgs: {...this.state.msgs, val} });
				new Audio(chatNotif).play();
			}
		});		
	}

	cleanBoards() {
		db.ref("boards").set(null);
	}
	
	cleanMsgs() {
		db.ref("msgs").set(null);
		this.setState({msgs:{}});
	}
	
	render() {
		var msgs = this.state.msgs;
		return (<>
		<button onClick={this.cleanBoards}>clean boards</button>
		<button onClick={this.cleanMsgs}>clean messages</button>
		<h1>Users messages</h1>
		<div aria-live="assertive">
			{Object.keys(msgs).map(k =>
				(<div key={k}><h2>{msgs[k].name}</h2>
				{textToBoardData(msgs[k].msg)}</div>
			))}
		</div></>);
	}
}

export default class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {user: null, showLogin: true}
	}

	componentDidMount() {
		firebase.auth().onAuthStateChanged(user => {
			this.setState({ user });
		});
	}

	handleSignIn() {
		const provider = new firebase.auth.GoogleAuthProvider();
		firebase.auth().signInWithPopup(provider);
	}

	handleLogOut() {
		firebase.auth().signOut();
	}

	render () {
		return (<div className="App">
			{ !this.state.user && this.state.showLogin? (<><button onClick={this.handleSignIn.bind(this)}>Sign in</button>
			<button onClick={() => this.setState({showLogin: false})}>dismis</button></>
			) : this.state.showLogin && (<button onClick={this.handleLogOut.bind(this)}>Logout</button>)}
			<MainBoard/>
			{getAdmin() && <AttendeesMsg/>}
		</div>);
	}
}

