import {textToBoardData, screenTest, parsedText} from './customFormat';

export class BoardStorage {
	constructor(title, content, parsedContent) {
		this.title = title;
		this.content = content;
		this.parsedContent = parsedContent;
		if (content) this.parsedContent = textToBoardData(content);
		this.lastFocusId = undefined;
	}
}

export const envStorage = {
	currentBoard: 0,
	lastId: 1,
	lastFocusId: undefined,
	lastBoardId: undefined,
	boards: [
		new BoardStorage("hola 1", undefined, screenTest),
		new BoardStorage("hola 2", undefined, parsedText)]
}

